import React from 'react'

import cursos from './styles/images/cursos_platzi.png'
import '../components/styles/header.css'
import './styles/platzi.css'


class Platzi extends React.Component{
    render(){
        return (
        <div className="section">
            <div className="section_content_platzi">
            <a href="https://platzi.com/c/referidoporseba/" target="_blank" rel="noreferrer">Obtén 1 mes gratis</a>
            <div className="fix_content_p">
            <img src={cursos} alt="cursos de platzi"></img>
            
            </div>
        </div>  
        </div>
        )
    }
}

export default Platzi