import React from "react";

import Navbar from './Navbar'
import MobileViewHeader from './MobileViewHeader'
import MobileViewTab from './MobileViewTab'
import Header from '../components/Header'
import '../pages/styles/home.css'
import { Link } from 'react-router-dom'


function Layout(props){
    if(window.screen.width < 600) {
        return (
            <div className="nav_container_mobile">
                    <MobileViewHeader />
                    <div className="propsStyle">
                    <div className="header__content_section">
                            <div className="links_section"><Link to="/ajedrez"><strong>AJEDREZ</strong></Link></div>
                            <div className="links_section"><Link to="/turismo"><strong>TURISMO</strong></Link></div>
                            <div className="links_section"><Link to="/contactos"><strong>CONTACTOS</strong></Link></div>
                    </div>
                        {props.children}
                        </div>
                    <MobileViewTab />
                </div>
        )
    }else{
        return (
            <div className="container">
                <div className="header_container">
                    <Header />           
                    {props.children}                                                       
                </div>
                <div className="nav_container">
                    <Navbar />
                </div>
            </div>
        )
    }
}

export default Layout