import React from 'react'


class Ajedrez extends React.Component {
    state = {
        loading: true,
        error: null,
        data: {}
    }
componentDidMount(){
    this.fetchIchess()
}

fetchIchess = async () => {
    this.setState({loading: true, error: null})

    try{
        const response = await fetch('https://play.chessbase.com?room=')
    this.setState({
        loading: false,
        data: response
    })
    }catch (error) {
        this.setState({
            loading: false,
            error: error
        })
    }
}

    render() {
        if (this.state.error) {
            return `Error: ${this.state.error.message}`
        }
        return (               
                <div className="section">
                    <div className="section_content">
                        <div className="fix_content_t" id="iframeDiv">
                        {this.state.loading && (
                                 <div className="heart">
                                 <h1>Cargando...</h1>
                             </div>
                            )}
                            <iframe src={this.state.data.url} frameborder="0" title="Pocamula ajedrez"></iframe>
                           
                        </div>                    
                    </div>
                </div> 
        )
    }
}

export default Ajedrez